  box-sizing: inherit;
  padding-left: 48px;
  padding-right: 20px;
  font-family: "PTMono", "Arial", sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  color: rgba(255, 255, 255, 0.8);
  cursor: pointer; }
  .popup__agreement:hover {
    color: #ffffff; }
  .popup__agreement span::before {
    position: absolute;
    width: 16px;
    height: 16px;
    top: 2px;
    left: 0;
    content: "";
    background-image: url("../img/checkbox-non-checked.svg"); }
  .popup__agreement input:checked + span::before {
    background-image: url("../img/checkbox-checked.svg"); }
